import { request } from 'utils/index';

const prefix = '/api/students';

export const getStudentById = ({ id } = {}) =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const updateStudent = ({
  id,
  candidateId,
  code,
  firstName,
  lastName,
  idCard,
  placeIssue,
  dateIssue,
  pobId,
  dob,
  gender,
  homeVillage,
  nationId,
  ethnicityId,
  religion,
  comeFromId,
  unionDate,
  politicalDate,
  permanentAddress,
  village,
  townId,
  districtId,
  policyObjectId,
  subsidizeObjectId,
  prioritizeObjectId,
  homePhone,
  phone,
  email,
  mailAddress,
  emergencyAddress,
  estimatedNote,
  foreignLanguage,
  liveAddress,
} = {}) =>
  request({
    prefix,
    url: `/Student/${id}`,
    method: 'PUT',
    params: {},
    data: {
      candidateId,
      code,
      firstName,
      lastName,
      idCard,
      placeIssue,
      dateIssue,
      pobId,
      dob,
      gender,
      homeVillage,
      nationId,
      ethnicityId,
      religion,
      comeFromId,
      unionDate,
      politicalDate,
      permanentAddress,
      village,
      townId,
      districtId,
      policyObjectId,
      subsidizeObjectId,
      prioritizeObjectId,
      homePhone,
      phone,
      email,
      mailAddress,
      emergencyAddress,
      estimatedNote,
      foreignLanguage,
      liveAddress,
    },
  });

export const updateStudentUploadAvatar = ({ id } = {}) =>
  request({
    prefix,
    url: `/Student/${id}/UploadAvatar`,
    method: 'PUT',
    params: {},
    data: {},
  });



